.gallery {
  background-image: linear-gradient(
    180deg,
    $primary-bg-color 25%,
    transparent 25%
  );
  display: flex;
  justify-content: center;
  @include mq(tablet-only) {
    background-image: linear-gradient(
      180deg,
      $primary-bg-color 40%,
      transparent 40%
    );
  }
}
.gallery-container {
  margin: 0 20px;
  @include mq(tablet) {
    margin: 0 32px;
  }
  @include mq(desktop) {
    margin: 0 100px;
  }
}
.gallery__slider-img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  transition-property: transform;
  transition-duration: 2000ms;
  @include mq(tablet) {
    border-radius: 18px;
  }
  @include mq(desktop) {
    border-radius: 24px;
  }
}
.gallery__slider-img:hover,
.gallery__slider-img:focus {
  transform: scale(1.1);
}
